/* index.css */
html, body, #root {
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f9f9f9;
    color: #333;
}

a {
    text-decoration: none;
    color: inherit;
}

nav a.active {
    font-weight: bold;
    color: #007bff;
}

.navbar {
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.logo {
  flex: 0 0 auto;
  font-weight: bold;
  font-size: 1.5rem;
}

.spacer {
  flex-grow: 1;
}

.hamburger {
  cursor: pointer;
  margin-left: 15px;
}



@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}



@media (max-width: 768px) {
    nav {
        flex-direction: column !important;
        align-items: flex-start !important;
    }

    nav h2 {
        margin-bottom: 10px;
    }

    nav div {
        flex-direction: column !important;
        gap: 10px !important;
    }

    .project-card {
        width: 90% !important;
    }

    .nav-links {
        display: none !important;
    }

    .hamburger {
        display: flex !important;
    }
    .navbar {
        padding: 0 15px;
    }
}
